html{padding:0}html body{margin:0;height:100%;width:100%}.container{display:grid;grid-template-columns:220px 1fr;position:relative;width:100%;height:100vh}.menu{width:220px;height:100%;top:0;left:0;background:#923DBF;line-height:55px;text-align:center}.trolla-title{width:100%;height:55px;justify-content:center;font-family:Pacifico;color:#fff;text-shadow:2px 2px 2px #612980;font-size:30px;font-style:italic;background:#612980;border-bottom:1px solid #612980}.menu-list{margin:0;padding:0;list-style-type:none;width:100%}.menu-item{margin:0;padding:0;font-size:14px;font-family:open sans, helvetica neue, Helvetica, Arial, sans-serif;font-weight:bold;color:#311440;text-align:left;padding-left:60px}.menu-item:hover{background:#AF49E6}.active-item{background:#AF49E6}a{text-decoration:none}.main{display:grid;grid-template-rows:auto auto 1fr;width:100%}.header{height:55px;border-bottom:1px solid #aaa;line-height:55px}.btn{height:20px;line-height:20px;border:1px solid #612980;border-radius:1px;background:#fff;padding:10px;font-size:15px;font-family:Open sans;color:#612980}.btn:hover{cursor:pointer;background:#612980;border-color:#612980;color:#fff;-webkit-transition:all ease-in-out .5s;transition:all ease-in-out .5s}.btn:focus{outline:none}.stream-toggle{position:absolute;right:15px;top:55px;-webkit-transform:translateY(-115%);transform:translateY(-115%)}.dropdown li{color:#612980;font-family:open sans;background:#fff}.dropdown li a{padding-left:10px}li{display:block;-webkit-transition-duration:0.5s;transition-duration:0.5s}.dropdown li:hover{cursor:pointer;background:#612980;color:#fff}.dropdown li:hover a{display:inline-block;width:100%;height:100%;color:#fff}ul li ul{padding:0;width:95px;visibility:hidden;opacity:0;position:absolute;-webkit-transition:all 0.5s ease;transition:all 0.5s ease;margin-top:33px;right:15px;display:none;border:1px solid #612980;text-align:left}ul li:hover>ul,ul li ul:hover{visibility:visible;opacity:1;display:block}ul li ul li{clear:both}.content-header{padding:18px;border-bottom:1px solid #aaa}.content-header .content-title{font-size:45px;font-weight:300}.content-header .content-title,.content-header .content-subtitle{font-family:open sans;color:#aaa}.content-header .content-subtitle{letter-spacing:2px;padding:0}.content-data{font-family:open sans;font-weight:300;margin:45px auto;border-collapse:collapse;width:80%}.content-data .table-header{background:#ccc;font-size:18px}.content-data th,.content-data td{padding:15px;border:1px solid #eee;text-align:center;vertical-align:middle}.content-data .table-row:nth-child(even){background:#ddd}.content-data .table-row::nth-child(odd){background:#fff}.content-footer{font-family:open sans;font-style:italic;color:#aaa;padding:18px;border-top:1px solid #eee}.content-footer span{font-family:Pacifico}

